import React from 'react';
// Styled Components
import { Progress } from '../../../styled/Skills';

function SkillProgressBar (props) {
    // Get circumference (2πr)
    let r = 35; //35
    let size = 40; //40
    let circumference = 2 * Math.PI * r;
    // Get stroke-dashoffset value based on the percentage of the circumference
    let strokeDashOffset = circumference - ((props.value * circumference) / 100);

    return (
        <Progress value={props.value} {...props} circumference={circumference} strokeDashOffset={strokeDashOffset}>
            <svg className="radial-progress" viewBox={`0 0 ${size * 2} ${size * 2}`}>
                <circle className="incomplete" cx={size} cy={size} r={r}></circle>
                <circle className="complete" cx={size} cy={size} r={r} ></circle>
                <text className="percentage" x="50%" y="57%" transform={`matrix(0, 1, -1, 0, ${size * 2}, 0)`}>{ props.value }%</text>
            </svg>
        </Progress>
    )
}

export default SkillProgressBar;