import React from 'react';
// import { SideBar } from './layouts';
import {
  AboutMe, 
  Experience, 
  Preparation,
  Skills,
  // Interests,
  Projects,
  Footer
} from './sections';

import { ThemeProvider } from 'styled-components';

import theme from './styled/theme';


function App() {
    
    const sections = [
        { hash: "about" , text: "About me", Section : AboutMe},
        { hash: "experience" , text: "Experience", Section : Experience},
        { hash: "education" , text: "Preparation", Section : Preparation},

        { hash: "projects" , text: "Projects", Section : Projects},
        // { hash: "interests" , text: "Interests", Section : Interests},
        { hash: "skills", text: "Skills", Section : Skills},
        { hash: "footer", text: "Footer", Section : Footer},

    ];

    return (
    <ThemeProvider theme={theme}>
      <div className="wrap" >
        {
          sections.map((section, index) => {
            const { Section, hash, ref } = section;
            return (
              <Section key={ hash + index} ref={ref} />
            )

          })
        }
      </div>
    </ThemeProvider>
    );
}

export default App;
