import React from 'react';
import _ from 'lodash';
// Components
import { SkillProgressBar } from '../../components/Skills';
// Styled Components
import { Section, SectionContainer, SectionTitle, DivClearBoth } from '../../styled/Common';
import { SkillsContainer, TechList } from '../../styled/Skills';

const technologies = [
    { name: "React", percent: 90, color: "#3498db" },
    { name: "Javascript", percent: 80, color: "#f1c40f" },
    { name: "NodeJS", percent: 80, color: "#27ae60" },
    { name: "HTML5 & CSS3 ", percent: 60, color: "#e67e22" },
    { name: "Docker", percent: 60, color: "#2980b9" },
    { name: "Python", percent: 60, color: "#d35400" },
    { name: "Firebase", percent: 50, color: "#f39c12" },
     { name: "AWS: S3/EC2", percent: 30, color: "#ff7f00" }
];

export default function Skills() {
    return (
        <Section minHeigth={"70vh"} id="skills" center>
            <SectionContainer
                display={"flex"}
                alignItems={"center"}
                justifyContent={"center"}
                flexDirection={"column"}
            >
                <SectionTitle color="white">Skills</SectionTitle>
                <SkillsContainer>
                    <TechList>
                        {
                            !_.isEmpty(technologies) && technologies.map(({ name, percent, color }, index) => (
                                <li key={index + name}>
                                        <div>{name}</div>
                                        <SkillProgressBar
                                            value={percent}
                                            color={color}
                                            text={name}
                                            width={"150px"}
                                        />
                                </li>
                            ))
                        }
                        <DivClearBoth />
                    </TechList>
                </SkillsContainer>
            </SectionContainer>
        </Section>
    )
}