import React, { useState, useEffect } from 'react';
import _ from 'lodash'
// Styled Components
import { Section, SectionContainer, DivClearBoth, SectionTitle } from '../../styled/Common';
// import { ExperienceTitle } from '../../styled/Experience';
import { ProjectsContainer, Wrap } from '../../styled/Projects';

// Components
import { ProjectCard } from '../../components/Projects';



export default function Projects () {
    const [ repos, setRepos ] = useState([]);
    const [ showing, setShowing] = useState(9);

    useEffect(()=>{
        const fetchRepos = async () =>{
            const headers = {
                'Authorization' : 'Bearer a99a45ffdd5ebc1e96177acf4163ecdd0f350af3'
            }
            const githubResponse = await fetch('https://api.github.com/users/toodaniels/repos?sort=updated', {headers});
            const reposUser = await githubResponse.json();
            setRepos(reposUser);
        }
        fetchRepos();
    }, []);

    const reposShowing = !_.isEmpty(repos) ? repos.slice(0, showing) : []; 

    return (
        <Section id="projects" white>
            <SectionContainer 
                display={"flex"}
                alignItems={"center"}
                justifyContent={"center"}
                flexDirection={"column"}
            >
                <SectionTitle >Open Source Projects</SectionTitle>
                
                    <ProjectsContainer> 
                        <Wrap>

                            {reposShowing.map((repo)=>(
                                <ProjectCard
                                    key={repo.id}
                                    repo={repo}
                                />
                            ))}
                        </Wrap>

                    
                        <DivClearBoth />
                        <div className={"action-container"}>
                            <button 
                                className={"show-more"}
                                // hidden={showing >= repos.length} 
                                onClick={()=> {
                                    showing >= repos.length ? setShowing(showing - 6) : setShowing(showing + (6));
                                }}
                            >                                
                                { showing >= repos.length ? "See less" : "See more"}
                            </button>
                        </div>

                    </ProjectsContainer>

            </SectionContainer>
        </Section>

    )
}