import React, { useEffect, useState } from 'react';
import _ from 'lodash';
// Components
import {
    FacebookShareButton,
    FacebookIcon,
    TwitterShareButton,
    TwitterIcon,
    WhatsappShareButton,
    WhatsappIcon,
    LinkedinShareButton,
    LinkedinIcon,
    EmailShareButton,
    EmailIcon
} from "react-share";
// Styled Components
import {
    CardDescription,
    CardMoreInfo
} from '../../../styled/Projects';

function ProjectCard(props) {
    const [ topics, setTopics ] = useState([]);
    const { repo } = props;
    const {
        name,
        forks_count,
        watchers_count,
        stargazers_count,
        description,
        html_url,
        homepage
    } = repo;

    const goto = (href) =>{
        window.location.href = href; 
    }

    useEffect(()=>{
        const fetchTopics = async () =>{
            const headers = {
                'Accept': 'application/vnd.github.mercy-preview+json',
                'Authorization' : 'Bearer 5ae8a4cb9de8e45648d3ad00aed70caa1457fc72'
            }
            let topicsResponse = await fetch(`https://api.github.com/repos/toodaniels/${name}/topics`, {headers});
            let topics = await topicsResponse.json();
            setTopics(topics["names"]);
        }
        //fetchTopics();
    }, [name])


    return (
        <>
            <CardDescription>
                <div className="repo-description">
                    <h3 className="title">{name}</h3>
                    <div className="description">
                         {description ? description : "No description available"}
                    </div>
                    <div className="stats">
                        <div>
                            <div className="title">Starts</div>
                            <i className="fas fa-star"></i>
                            <div className="value">{stargazers_count}</div>
                        </div>
                        <div>
                            <div className="title">Forks</div>
                            <i className="fas fa-code-branch"></i>
                            <div className="value">{forks_count}</div>
                        </div>
                        <div>
                            <div className="title">Watchers</div>
                            <i className="fas fa-eye"></i>
                            <div className="value">{watchers_count}</div>
                        </div>
                    </div>
                </div>
                <CardMoreInfo>
                    <h3> Share { name }</h3>
                    
                     <div>
                         <FacebookShareButton
                             url={html_url}
                             quote={name}
                         >
                             <FacebookIcon size={32} round />
                         </FacebookShareButton>
                         <TwitterShareButton
                             url={html_url}
                             title={name}
                         >
                             <TwitterIcon size={32} round />
                         </TwitterShareButton>
                         <WhatsappShareButton
                             url={html_url}
                             title={name}
                             separator=":: "
                         >
                             <WhatsappIcon size={32} round />
                         </WhatsappShareButton>
                         <LinkedinShareButton url={html_url} >
                             <LinkedinIcon size={32} round />
                         </LinkedinShareButton>
                         <EmailShareButton
                             url={html_url}
                             subject={name}
                             body="body"
                         >
                             <EmailIcon size={32} round />
                         </EmailShareButton>
                     </div>
                     {/* <h3> Topics</h3> */}

                     <div className="topics-container">
                     {
                         !_.isEmpty(topics) && topics.map((topic, index)=>(
                             <span key={index + topic}>#{topic} </span>
                         ))
                     }
                     </div>
                     <h3> Go to </h3>
                     <div className="goto-container">
                         <button onClick={() => goto(html_url)} >
                             <i className="fab fa-github"></i>
                         </button>
                         {!_.isEmpty(homepage) && <button onClick={ ()=> goto(homepage)}>
                             <i className="fas fa-globe"></i>
                         </button>}
                     </div>
                    
                 </CardMoreInfo>
                
            </CardDescription>
        </>
    )
}

export default ProjectCard;